import {Table} from 'react-bootstrap';
import { useEffect, useState } from 'react';
import axios from 'axios';

function Cart() {
    const [list, setList] = useState([]);

    useEffect(() => {
        const user = JSON.parse(sessionStorage.getItem('loginUser'));

        if(!user) {
            console.log('  ');
            return;
        }

        axios.get('/react/getCart', {params:{memId: user.email}})
             .then(result => {
                setList(result.data);
             })
             .catch(() => {
                console.log("");
             })
    }, []);

    return (
        <div className='cart'>
            <br/>
            <h2>CART LIST</h2>
            <br/>

            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        list.map(c =>
                            <tr>
                                <td>{c.id}</td>
                                <td>{c.title}</td>
                                <td>{c.count}</td>
                            </tr>
                        )
                    }
                </tbody>
            </Table>
        </div>
    )
}
export default Cart;