import axios from 'axios';
import { useState } from 'react';
import {Col, Form, Row, Button, Modal} from 'react-bootstrap';
import DaumPostcode from 'react-daum-postcode';

const Signup = () => {
    const [form, setForm] = useState({
        email : '',
        password : '',
        name : '',
        birthday : '',
        gender : '',
        phone : '',
        address : '',
        detailAddress : ''
    })

    const handleChange = (e) => {
        const {name, value} = e.target;
        setForm({
            ...form,
            [name] : value
        })
        if(name == 'email') {
            setEmailCheckMessage('');
            setIsEmailChecked(false);
        }
    }
    const [emailCheckMessage, setEmailCheckMessage] = useState('');
    const [isEmailChecked, setIsEmailChecked] = useState(false);
    const [showPostcode, setShowPostcode] = useState(false)
    const checkEmail = () => {
        axios.get('/react/email-check', {params: {email:form.email}})
             .then(result => {
                if(result.data) {
                    setEmailCheckMessage('   .');
                    setIsEmailChecked(true);
                } else {
                    setEmailCheckMessage('    .');
                    setIsEmailChecked(false);
                }
             })
             .catch(() => {
                alert('    .');
             })
    }

    const handleComplete = (data) => {
        setForm({...form, address: data.address});
        setShowPostcode(false);
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        if(!isEmailChecked) {
            alert("   .");
            return;
        }
        axios.post('/react/signup', form)
             .then(() => {
                alert(' ');
                window.location.href = '/';
             })
             .catch(() => alert(' '))
    }

    return (
        <div style={style.container}>
            <h2 style={style.title}></h2>
            <Form style={style.form} onSubmit={handleSubmit}>
                <Form.Group as={Row} className="mb-3" >
                    <Form.Label column sm="2">EMAIL</Form.Label>
                    <Col sm="8">
                        <Form.Control type="email" name="email" onChange={handleChange} required />
                    </Col>
                    <Col sm="2">
                        <Button onClick={checkEmail}></Button>
                    </Col>
                    {
                        emailCheckMessage && (
                            <span style={{color: isEmailChecked ? 'green' : 'red', fontSize:'0.8em', marginTop:'5px'}}>
                                {emailCheckMessage}
                            </span>
                        )
                    }
                </Form.Group>

                <Form.Group as={Row} className="mb-3" >
                    <Form.Label column sm="2">PASSWORD</Form.Label>
                    <Col sm="10">
                    <Form.Control type="password" name="password" onChange={handleChange} required/>
                    </Col>
                </Form.Group>

                <Form.Group as={Row} className="mb-3" >
                    <Form.Label column sm="2">NAME</Form.Label>
                    <Col sm="10">
                    <Form.Control name="name" onChange={handleChange} required/>
                    </Col>
                </Form.Group>

                <Form.Group as={Row} className="mb-3" >
                    <Form.Label column sm="2">BIRTHDAY</Form.Label>
                    <Col sm="10">
                    <Form.Control type="date" name="birthday" onChange={handleChange} />
                    </Col>
                </Form.Group>

                <Form.Group as={Row} className="mb-3" >
                    <Form.Label column sm="2">GENDER</Form.Label>
                    <Col sm="10">
                    <Form.Select name="gender" onChange={handleChange}>
                            <option> </option>
                            <option value="M"></option>
                            <option value="F"></option>
                    </Form.Select>
                    </Col>
                </Form.Group>

                <Form.Group as={Row} className="mb-3" >
                    <Form.Label column sm="2">PHONE</Form.Label>
                    <Col sm="10">
                    <Form.Control name="phone" onChange={handleChange} />
                    </Col>
                </Form.Group>

                <Form.Group as={Row} className="mb-3" >
                    <Form.Label column sm="2">ADDRESS</Form.Label>
                    <Col sm="8">
                    <Form.Control name="address" value={form.address} readOnly />
                    </Col>
                    <Col sm="2">
                    <Button onClick={() => setShowPostcode(true)}> </Button>
                    </Col>
                </Form.Group>

                <Form.Group as={Row} className="mb-3" >
                    <Form.Label column sm="2">DETAIL ADDRESS</Form.Label>
                    <Col sm="10">
                    <Form.Control name="detailAddress" onChange={handleChange}/>
                    </Col>
                </Form.Group>
                <Button type="submit"></Button>
            </Form>

            <Modal show={showPostcode} onHide={() => setShowPostcode(false)}>
                <Modal.Header closeButton>
                    <Modal.Title> </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <DaumPostcode onComplete={handleComplete}/>
                </Modal.Body>
            </Modal>
        </div>
    )
}

const style = {
    container : {
        textAlign : 'center',
        marginTop : '40px',
    },
    title : {
        textAlign: 'center',
        marginBottom : '20px'
    },
    form : {
        margin: '0 auto',
        width : '80%'
    }
}

export default Signup;